import { injectGlobal } from 'styled-components';
import { richtBlue, richBlack } from 'theme/variables';

injectGlobal`
@import url('https://fonts.googleapis.com/css?family=Zilla+Slab+Highlight:700');

  h1 {
    font-family: 'Zilla Slab Highlight', cursive;
    font-weight: 700;
    color: ${richtBlue};
  }

  @import url('https://fonts.googleapis.com/css?family=Slabo+13px');
  body {
    font-family: 'Slabo 13px', serif;
    font-size: 18pt;
    color: ${richBlack};
  }
`



