import styled from 'styled-components';
import { richBlack } from 'theme/variables';
import { richtBlue } from 'theme/variables';
import { bondiBlue } from 'theme/variables';
import { sapphire } from 'theme/variables';


export const Background = styled.div`
    position: fixed;
    left: 10vw;
    width: 80vw;
    top: 10vh;
    height: 88vh;
    background-color: ${bondiBlue};
    opacity: 0.2;
    background-size: 80%;
    background-repeat: no-repeat;
    background-position: center;
    z-index: -99;
`;



