import React, { Component, PropTypes } from 'react';
import {Container} from 'theme/grid';
import {
  Image,
  HeroImage,
  RevealP,
  LogoImage
} from './Home.style';
import WhenInView from 'components/WhenInView/WhenInView'


export default class Home extends Component {
  static propTypes = {};

  render() {
    return (
      <Container>
        <HeroImage>
          <h1>Chaotic Eden</h1>
          <h1>Design</h1>
        </HeroImage>
        {/*}<Image src={require('../../assets/himal-cine-blue.gif')} alt="testing" />*/}
        <WhenInView>
          {({ isInView }) =>

          <RevealP hide={!isInView}>Hi! My name is Sharon and I'm a digital designer based in London.
          I design and build Web Sites. I love motion graphics, and photography. 
          </RevealP>
        }
        </WhenInView>
        
      </Container>
        );
      }
    }



