import styled from 'styled-components';
import { Flex, Div } from 'theme/grid';
import { richtBlue, bondiBlue, sapphire, richBlack } from 'theme/variables';
import { Link } from 'react-router';


export const NavigationContainer = styled(Flex)`
    position: absolute;
    left: 5.25em;
    top: 0.3em;
    color: ${sapphire};
    
`;

export const NavItem = styled(Link)`
    margin-right: 30px;
    font-size: 1.2em;
    cursor: pointer;
    color: ${richBlack};
    position: relative;
    text-decoration: none;

    &:hover {
        color: ${bondiBlue};
        &:after {
            content: ' ';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background-colour: ${sapphire};
            z-index: -1;
            transform: scale(1.3,1.5);
        }
        // &:hover:after {
        //     height: 100%;
        //     background-color: ${richtBlue};
        //   }
    }
`;


