import styled, { css } from 'styled-components';
import { richtBlue, bondiBlue, sapphire, richBlack } from 'theme/variables';
import { Relative } from 'theme/grid';



export const Index = styled.div`
    position: absolute;
    left: 0;
    bottom: 10px;
    z-index: -1;
    h1 {
      font-size: 2.5em;
      margin: 0;
      font-weight: 800;
      color: ${sapphire};
      opacity: 0.4;
      transform: translateY(20%);
    }

`;

export const ImageContainer = styled.div`
    width: 800px;
    margin-left: auto;
    margin-right: auto;
`;

export const Title = styled.h1`
    color: ${richBlack};
    font-weight: bold;
    margin-top: 80px;
`;

export const Item = styled.h2`
    font-weight: light;
    font-size: 0.9em;
`;

export const VideoContainer = styled(Relative)`
width: 70%;
flex-shrink: 0;
flex-grow: 0;
cursor: pointer;
`;


